.iconWrapper {
  position: relative;
  width: 300px;
  margin: auto;
}

.iconImg {
  width: 100%;
  height: auto;
  display: block;
  margin: auto;
}

.iconLabel {
  background: red;
  color: #ffffff;
  font-weight: 400;
  font-size: 24px;
  display: block;
  border: 2px solid #fff;
  border-radius: 100px;
  overflow: hidden;
  padding: 6px 10px;
  position: absolute;
  top: 10px;
  right: -10px;
  transform-origin: left bottom;
}

.iconLabel1 {
  animation: rotate infinite 1s forwards, disappear infinite 2s forwards;
}
.iconLabel2 {
  animation: rotate infinite 1s forwards, appear infinite 2s forwards;
}

@keyframes rotate {
  0% {
    transform: rotate(-16deg);
  }
  60% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes disappear {
  0% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes appear {
  0% {
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

